import { Divider, Menu, MenuItem } from "@material-ui/core";
import { duplicateTab, moveToNewWindow, moveToWindow, reloadTab, toggleMuteTab, togglePinTab, translate, ungroupTab } from "../helpers/chrome";
import { ChromeTab, ChromeWindow, ContextMenuPosition, GroupedTabs } from "../Types";
import { NestedMenuItem } from "./NestedMenuItem";
import { useMemo } from "react";

type TabContextMenuProps = {
  tab: ChromeTab;
  selectedTabs?: ChromeTab[];
  onClose(id: number): void;
  onCloseOtherTabs(keepTabs: number[]): void;
  onCloseBelowTab(tab: ChromeTab): void;
  onNewGroupCreated(groupId: number): void;
  windows: ChromeWindow[];
  groupedTabs: GroupedTabs[];
  contextMenuPosition?: ContextMenuPosition | null;
  closeMenu: () => void;
};

const TabContextMenu: React.FC<TabContextMenuProps> = ({ tab, selectedTabs, contextMenuPosition, closeMenu, onClose, onCloseOtherTabs, onCloseBelowTab, windows, onNewGroupCreated, groupedTabs }) => {
  const multipleTabsSelected = selectedTabs?.length && selectedTabs.some(t => t.id === tab.id);

  const showPinAction = useMemo(() => {
    if (multipleTabsSelected) {
      return selectedTabs.some(t => !t.pinned);
    } else {
      return !tab.pinned;
    }
  }, [multipleTabsSelected, selectedTabs, tab.pinned]);

  const showMuteAction = useMemo(() => {
    if (multipleTabsSelected) {
      return selectedTabs.some(t => !t.mutedInfo?.muted);
    } else {
      return !tab.mutedInfo?.muted;
    }
  }, [multipleTabsSelected, selectedTabs, tab.mutedInfo?.muted]);


  if (!contextMenuPosition) {
    return null;
  }

  const tabs = multipleTabsSelected ? selectedTabs : [tab];

  const handleReload = () => {
    closeMenu();

    tabs.forEach(tab => tab.id && reloadTab(tab.id));
  };

  const handleDuplicate = () => {
    closeMenu();

    tabs.forEach(tab => tab.id && duplicateTab(tab.id));
  };

  const handlePin = () => {
    closeMenu();

    tabs.forEach(tab => tab.id && togglePinTab(tab.id, true));
  };

  const handleUnpin = () => {
    closeMenu();

    tabs.forEach(tab => tab.id && togglePinTab(tab.id, false));
  }

  const handleMute = () => {
    closeMenu();

    tabs.forEach(tab => tab.id && toggleMuteTab(tab.id, true));
  };

  const handleUnmute = () => {
    closeMenu();

    tabs.forEach(tab => tab.id && toggleMuteTab(tab.id, false));
  };

  const handleNewTabBelow = () => {
    closeMenu();

    chrome.tabs.create({ index: tab.index + 1 });
  };

  const handleUngroup = () => {
    closeMenu();

    ungroupTab(tabs.map(t => t.id!));
  };

  const handleMoveToWindow = (window: ChromeWindow) => {
    closeMenu();

    if (window.id) {
      tabs.forEach(tab => tab.id && moveToWindow(tab.id, window.id!));
    }
  };

  const handleMoveToNewWindow = async () => {
    closeMenu();

    // move first tab to new window
    const newWindow = await moveToNewWindow(tabs[0].id!);

    // move the rest
    if (tabs.length > 1) {
      tabs.slice(1).forEach(tab => tab.id && moveToWindow(tab.id, newWindow.id!));
    }
  };

  const handleClose = () => {
    closeMenu();

    if (tab.id) {
      onClose(tab.id);
    }
  }

  const handleCloseOthers = () => {
    closeMenu();

    onCloseOtherTabs(tabs.map(t => t.id!));
  };

  const handleCloseBelow = () => {
    closeMenu();

    const tabToCloseBelow = multipleTabsSelected ? selectedTabs[selectedTabs.length - 1] : tab;
    onCloseBelowTab(tabToCloseBelow);
  };

  // move to new/existing group (if existing it will automatically ungroup it from old)
  const handleMoveToGroup = async (groupId?: number) => {
    const newGroupId = await chrome.tabs.group({ tabIds: tabs.map(t => t.id!), groupId });

    if (groupId === undefined) {
      onNewGroupCreated(newGroupId);
    }

    closeMenu();
  }

  const getWindowTitle = (window: ChromeWindow) => {
    if (!window?.tabs || !window.tabs?.length) {
      return "";
    }

    if (window.tabs?.length === 1) {
      return window.tabs[0].title;
    }

    const activeTab = window.tabs?.find((t) => t.active);
    const concatTabTitle = activeTab?.title ?? '';//?.slice(0, 15);
    const tabCount = window.tabs?.length - 1;
    return translate(tabCount === 1 ?
      'tab_title_and_other_tab_count' :
      'tab_title_and_other_tabs_count',
      [concatTabTitle, tabCount.toString()]);
  };

  const getGroupTitle = (groupedTabs: GroupedTabs) => {
    if (groupedTabs.group.title) {
      return groupedTabs.group.title;
    }

    if (groupedTabs.tabs?.length === 1) {
      return groupedTabs.tabs[0].title;
    }

    const firstTab = groupedTabs.tabs[0];
    const concatTabTitle = firstTab?.title;
    const tabCount = groupedTabs.tabs?.length - 1;
    const otherText = tabCount === 1 ? "Other Tab" : "Other Tabs";

    return [concatTabTitle, `${tabCount} ${otherText}`].join(" and ");
  }

  const otherGroups = tab.groupId !== -1 ? groupedTabs?.filter(g => tab.groupId !== g.id) : groupedTabs;
  const closeBelowDisabled = tabs[tabs.length - 1].isLastTab;
  const showUngroup = tabs.some(t => t.groupId !== -1);

  return (
    <Menu
      open={contextMenuPosition !== null}
      onClose={closeMenu}
      anchorReference="anchorPosition"
      anchorPosition={
        contextMenuPosition !== null
          ? { top: contextMenuPosition.mouseY, left: contextMenuPosition.mouseX }
          : undefined
      }
      MenuListProps={{
        dense: true,
      }}
      PaperProps={{
        elevation: 10,
        style: { backgroundColor: "transparent" },
      }}
    >
      <MenuItem onClick={handleNewTabBelow}>{translate('new_tab_below')}</MenuItem>
      {!!otherGroups.length && <NestedMenuItem
        label={translate(multipleTabsSelected ? 'add_tabs_to_group' : 'add_tab_to_group')}
        parentMenuOpen={contextMenuPosition !== null}
      >
        <>
          <MenuItem onClick={() => handleMoveToGroup()}>{translate('new_group')}</MenuItem>
          <Divider />
          {otherGroups.map(g => (
            <MenuItem key={g.id} onClick={() => handleMoveToGroup(g.id)}>
              {getGroupTitle(g)}
            </MenuItem>
          ))}
        </>
      </NestedMenuItem>}
      {!otherGroups.length && <MenuItem onClick={() => handleMoveToGroup()}>{translate(multipleTabsSelected ? 'add_tabs_to_new_group' : 'add_tab_to_new_group')}</MenuItem>
      }
      {showUngroup && (
        <MenuItem onClick={handleUngroup}>{translate('remove_from_group')}</MenuItem>
      )}
      {windows.length > 1 && (
        <NestedMenuItem
          label={translate(multipleTabsSelected ? 'move_tabs_to_another_window' : 'move_tab_to_another_window')}
          parentMenuOpen={contextMenuPosition !== null}
        >
          <>
            <MenuItem onClick={handleMoveToNewWindow}>{translate('new_window')}</MenuItem>
            <Divider />
            {windows &&
              windows
                .filter((w) => w.id !== tab.windowId)
                .map((w) => (
                  <MenuItem key={w.id} onClick={() => handleMoveToWindow(w)}>
                    {getWindowTitle(w)}
                  </MenuItem>
                ))}
          </>
        </NestedMenuItem>
      )}
      {windows.length === 1 && (
        <MenuItem onClick={handleMoveToNewWindow}>
          {translate(multipleTabsSelected ? 'move_tabs_to_new_window' : 'move_tab_to_new_window')}
        </MenuItem>
      )}
      <Divider />
      <MenuItem onClick={handleReload}>{translate('reload')}</MenuItem>
      <MenuItem onClick={handleDuplicate}>{translate('duplicate')}</MenuItem>
      {showPinAction && <MenuItem onClick={handlePin}>{translate('pin')}</MenuItem>}
      {!showPinAction && <MenuItem onClick={handleUnpin}>{translate('unpin')}</MenuItem>}
      {showMuteAction && <MenuItem onClick={handleMute}>{translate(multipleTabsSelected ? 'mute_sites' : 'mute_site')}</MenuItem>}
      {!showMuteAction && <MenuItem onClick={handleUnmute}>{translate(multipleTabsSelected ? 'unmute_sites' : 'unmute_site')}</MenuItem>}
      <Divider />
      <MenuItem onClick={handleClose}>{translate('close')}</MenuItem>
      <MenuItem onClick={handleCloseOthers}>{translate('close_other_tabs')}</MenuItem>
      <MenuItem disabled={closeBelowDisabled} onClick={handleCloseBelow}>{translate('close_tabs_below')}</MenuItem>
    </Menu>
  );
};

export default TabContextMenu;
