import { ChromeTab, ChromeTabGroup, ChromeTabOrGroupedTabs, ChromeWindow, ContextMenuPosition, GroupedTabs, Settings, TabList } from "../Types";
import Tab from './Tab';
import {
  DndContext,
  PointerSensor,
  useSensor,
  useSensors,
  DragEndEvent,
  DragStartEvent,
  DragOverEvent,
  DragCancelEvent,
  rectIntersection,
  MeasuringStrategy,
  DragMoveEvent,
} from '@dnd-kit/core';
import {
  SortableContext,
  useSortable,
  verticalListSortingStrategy,
} from '@dnd-kit/sortable';
import { ReactNode, useEffect, useMemo, useRef, useState } from 'react';
import { CSS } from "@dnd-kit/utilities";
import styled from "styled-components";
import { restrictToVerticalAxis } from "@dnd-kit/modifiers";
import React from "react";
import { getGroupColor } from "../helpers/getGroupColor";
import TabContextMenu from "./TabContextMenu";
import TabGroupContextMenu from "./TabGroupContextMenu";
import PinnedTab from "./PinnedTab";
import { Divider } from "@material-ui/core";
import { useScrollObserver } from "../hook/useScrollObserver";
import NewTabButton from "./NewTabButton";
import TabGroup from "./TabGroup";
import { tabContextMenuViewed, tabGroupContextMenuViewedEvent } from "../helpers/gaEvents";

type TabListProps = {
  tabList: TabList;
  tabs: ChromeTab[];
  onTabMove(event: DragEndEvent): void;
  onTabDrag?(event: DragOverEvent): void;
  onDragStart(event: DragStartEvent): void;
  onDragCancel(event: DragCancelEvent): void;
  onClick(tabId?: number): void,
  onClose(tabId?: number): void,
  onGroupCollapseChange(group: ChromeTabGroup): void,
  onNewTab(): void;
  onGroupNewTab(groupedTabs?: GroupedTabs): void;
  onCloseOtherTabs(keepTabs: number[]): void;
  onCloseBelowTab(tab: ChromeTab): void;
  onDragMove?(event: DragMoveEvent): void;
  settings: Settings;
  activeItem: ChromeTabOrGroupedTabs | null;
  windows: ChromeWindow[];
}

const PinnedTabsContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  padding: 5px 15px;
`;

const ScrollableList = styled.div`
  overflow-y: auto;
  flex: 1;
  padding-top: 10px;

  &::-webkit-scrollbar {
    width: 6px;
    height: 6px;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--secondary-color);
    border-radius: 10px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: var(--secondary-color);
  }

  &::-webkit-scrollbar-track {
    background: var(--background-color);
    border-radius: 10px;
  }
`;

const SortableItem = (props: { children?: ReactNode; id: number, parentId?: number, type: string }) => {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
  } = useSortable({
    id: props.id,
    data: {
      id: props.id,
      parent: props.parentId,
      type: props.type
    },
  });

  const style = {
    transform: CSS.Translate.toString(transform),
    transition,
  };

  return (
    <div ref={setNodeRef} style={style} {...attributes} {...listeners}>
      {props.children}
    </div>
  );
}

const TabsList: React.FC<TabListProps> = ({
  tabList,
  tabs,
  settings,
  activeItem,
  windows,
  onTabMove,
  onTabDrag,
  onClick,
  onClose,
  onNewTab,
  onGroupCollapseChange,
  onGroupNewTab,
  onDragStart,
  onDragCancel,
  onCloseOtherTabs,
  onCloseBelowTab,
  onDragMove,
}) => {
  const sensors = useSensors(useSensor(PointerSensor, {
    activationConstraint: { distance: 1 },
  }));
  const [activeMiddleMouseTab, setActiveMiddleMouseTab] = useState<number | null>(null);
  const [selectedTabs, setSelectedTabs] = useState<number[]>([]);
  const [lastSelectedTab, setLastSelectedTab] = useState<ChromeTab | null>(null);

  const getItems = () => {
    return getTabItems().reduce<number[]>((acc, groupedTab) => {
      if ('group' in groupedTab) {
        acc.push(groupedTab.group.id);
        if (!(activeItem instanceof GroupedTabs)) {
          groupedTab.tabs.forEach((val) => acc.push(val.id!));
        }
      } else {
        acc.push(groupedTab.id!);
      }

      return acc;
    }, []);
  }

  const handleNewGroupCreated = async (groupId: number) => {
    const group = await chrome.tabGroups.get(groupId);
    const tabs = await chrome.tabs.query({ groupId })

    setCurrentContextualGroupedTabs(new GroupedTabs({
      group,
      tabs
    }));

    if (tabContextMenuPosition !== null) {
      setGroupContextMenuPosition({
        mouseX: tabContextMenuPosition.mouseX,
        mouseY: tabContextMenuPosition.mouseY
      });
    }
    tabGroupContextMenuViewedEvent();
  }

  const handleMouseDown = (event: React.MouseEvent, tab: ChromeTab) => {
    if (event.button === 1 && tab.id) {
      event.preventDefault();
      setActiveMiddleMouseTab(tab.id);
    }
  }

  const handleMouseUp = (event: React.MouseEvent, tab: ChromeTab) => {
    if (event.button === 1 && activeMiddleMouseTab === tab.id) {
      onClose(tab.id);
    }

    if (activeMiddleMouseTab !== null) {
      setActiveMiddleMouseTab(null);
    }
  }

  const clearSelectedTabs = () => {
    if (selectedTabs.length) {
      setSelectedTabs([]);
    }
  }

  const handleTabClick = (tab: ChromeTab, e: React.MouseEvent) => {
    const { shiftKey, metaKey, ctrlKey } = e;
    const isCtrlKey = metaKey || ctrlKey;

    if (lastSelectedTab && shiftKey) {
      const startIndex = Math.min(lastSelectedTab.index, tab.index);
      const endIndex = Math.max(lastSelectedTab.index, tab.index);

      setSelectedTabs(tabs.slice(startIndex, endIndex + 1).map(t => t.id!));
      onClick(tab.id);
    } else if (isCtrlKey) {
      if (selectedTabs.includes(tab.id!)) {
        setSelectedTabs(selectedTabs.filter(id => id !== tab.id));
        setLastSelectedTab(tab);

        // select another tab when unselecting active tab
        if (tab.active && selectedTabs.length) {
          const tabIdToSelect = selectedTabs.find(id => id !== tab.id);

          if (tabIdToSelect) {
            onClick(tabIdToSelect);
          }
        }
      } else {
        if (!selectedTabs.length && lastSelectedTab?.id) {
          setSelectedTabs([lastSelectedTab.id, tab.id!])
        } else {
          setSelectedTabs([...selectedTabs, tab.id!]);
        }
        onClick(tab.id);
      }
    } else {
      setLastSelectedTab(tab);
      clearSelectedTabs();
      onClick(tab.id);
    }
  }

  const renderTab = (tab: ChromeTab, color?: string) => {
    return (
      <div ref={el => itemsRef.current[tab.index] = el}>
        <Tab
          tab={tab}
          isCompact={settings.compactViewEnabled}
          showFullUrl={settings.showFullUrl}
          selected={selectedTabs.includes(tab.id!)}
          color={color}
          onClick={(e) => handleTabClick(tab, e)}
          onClose={() => onClose(tab.id)}
          onMouseDown={(e) => handleMouseDown(e, tab)}
          onMouseUp={(e) => handleMouseUp(e, tab)}
          onContextMenu={(e) => handleTabContextMenu(e, tab)}
          isDragging={activeItem?.id === tab.id}
        />
      </div >
    );
  }

  const renderItem = (item: ChromeTabOrGroupedTabs) => {
    if (item instanceof GroupedTabs) {
      const isGroupActive = activeItem instanceof GroupedTabs;
      const groupColor = getGroupColor(item.group?.color);

      return (
        <React.Fragment key={item.group.id}>
          <SortableItem id={item.group?.id} type="group">
            <TabGroup
              group={item.group}
              color={groupColor}
              onCollapseChange={() => onGroupCollapseChange(item.group)}
              forceCollapse={activeItem?.id === item.id ? true : undefined}
              onNewTab={() => onGroupNewTab(item)}
              onContextMenu={(e) => handleGroupContextMenu(e, item)}
            >
              {isGroupActive &&
                item.tabs.map(tab =>
                  <React.Fragment key={tab.id}>
                    {renderTab(tab, groupColor)}
                  </React.Fragment>
                )}
            </TabGroup>
          </SortableItem>
          {!isGroupActive && !item.group.collapsed && item.tabs.map(tab =>
            <SortableItem key={tab.id} id={tab.id!} parentId={item.id} type="tab">
              {renderTab(tab, groupColor)}
            </SortableItem>
          )}
        </React.Fragment>
      );
    }

    return (
      <SortableItem key={item.id} id={item.id!} type="tab">
        {renderTab(item)}
      </SortableItem>
    );
  }

  // filter out pinned items if pinnedTabsAsIcons is enabled
  const getTabItems = () => {
    if (settings.pinnedTabsAsIcons) {
      return tabList.filter(item => {
        if ('active' in item && item.pinned) {
          return false;
        }

        return true;
      })
    }

    return tabList;
  }

  const itemsRef = useRef<Array<HTMLDivElement | null>>([]);
  const activeTab = tabs.find(tab => tab.active);

  useEffect(() => {
    itemsRef.current = itemsRef.current.slice(0, tabs.length);

    if (activeTab) {
      const listContainer = scrollableListRef.current;
      const containerRect = listContainer?.getBoundingClientRect();
      const itemRef = itemsRef.current.at(activeTab.index);
      const itemRect = itemRef?.getBoundingClientRect();
      if (containerRect && itemRect) {
        const tabOutOfView = itemRect.top < containerRect.top ||
          itemRect.bottom > containerRect.bottom;
        if (tabOutOfView) {
          setTimeout(() => {
            itemRef?.scrollIntoView({
              behavior: 'instant',
              block: 'center'
            });
          }, 100);
        }
      }
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [activeTab?.id]);

  const renderGroupedTabs = () => {
    return (
      <>
        {getTabItems().map(
          item => renderItem(item)
        )}
      </>
    );
  }

  const renderPinnedTabs = () => {
    const pinnedTabs = tabList.filter(tab => 'active' in tab && tab.pinned) as ChromeTab[];

    if (settings.pinnedTabsAsIcons && pinnedTabs.length) {
      return (
        <div>
          <PinnedTabsContainer>
            <DndContext
              sensors={sensors}
              collisionDetection={rectIntersection}
              onDragEnd={onTabMove}
              onDragStart={onDragStart}
              onDragMove={onDragMove}
              onDragOver={onTabDrag}
              onDragCancel={onDragCancel}
              measuring={{ droppable: { strategy: MeasuringStrategy.WhileDragging } }}
            >
              <SortableContext
                items={pinnedTabs.map(t => t.id!)}
              >
                {pinnedTabs.map(tab =>
                  <SortableItem key={tab.id} id={tab.id!} type="tab">
                    <PinnedTab
                      tab={tab}
                      selected={selectedTabs.includes(tab.id!)}
                      onClick={(e) => handleTabClick(tab, e)}
                      onMouseDown={(e) => handleMouseDown(e, tab)}
                      onContextMenu={(e) => handleTabContextMenu(e, tab)}
                      onMouseUp={(e) => handleMouseUp(e, tab)}
                      isDragging={activeItem?.id === tab.id}
                    />
                  </SortableItem>
                )}
              </SortableContext>
            </DndContext>
          </PinnedTabsContainer>
          <Divider />
        </div>
      );
    }

    return null;
  }

  const [isScrolling, setIsScrolling] = useState<boolean>(false);
  const handleScroll = ({ isScrolling }: { isScrolling: boolean }) => {
    setIsScrolling(isScrolling);
  }

  const scrollableListRef = useRef<HTMLDivElement>(null);
  useScrollObserver(scrollableListRef, handleScroll);

  const [tabContextMenuPosition, setTabContextMenuPosition] = useState<ContextMenuPosition | null>(null);
  const [groupContextMenuPosition, setGroupContextMenuPosition] = useState<ContextMenuPosition | null>(null);
  const [currentContextualTab, setCurrentContextualTab] = useState<ChromeTab | null>(null);
  const [currentContextualGroupedTabs, setCurrentContextualGroupedTabs] = useState<GroupedTabs | null>(null);

  const handleGroupContextMenu = (event: React.MouseEvent, groupedTabs: GroupedTabs) => {
    event.preventDefault();
    setCurrentContextualGroupedTabs(groupedTabs);

    const elRect = event.currentTarget.getBoundingClientRect();
    setGroupContextMenuPosition({
      mouseX: elRect.left + 30,
      mouseY: elRect.bottom + 5
    });
    tabGroupContextMenuViewedEvent();
  };

  const handleTabContextMenu = (event: React.MouseEvent, tab: ChromeTab) => {
    event.preventDefault();
    setCurrentContextualTab(tab);
    setTabContextMenuPosition(
      tabContextMenuPosition === null
        ? {
          mouseX: event.clientX + 2,
          mouseY: event.clientY - 6,
        }
        : null
    );
    tabContextMenuViewed();
  };

  const handleTabCloseContextMenu = () => {
    setTabContextMenuPosition(null);
  };

  const handleGroupCloseContextMenu = () => {
    setGroupContextMenuPosition(null);
  };

  // needed to get updated group attributes
  const currentContextMenuGroup = useMemo(() => {
    if (currentContextualGroupedTabs) {
      const groupedTabs = tabList.find(t => t.id === currentContextualGroupedTabs.id) as GroupedTabs

      return groupedTabs?.group;
    }

    return null;
  }, [currentContextualGroupedTabs, tabList]);

  const handleNewTab = () => {
    if (selectedTabs.length) {
      clearSelectedTabs();
    }

    onNewTab();
  }

  return (
    <>
      {currentContextualTab && <TabContextMenu
        tab={currentContextualTab}
        selectedTabs={tabs.filter(t => selectedTabs.includes(t.id!))}
        contextMenuPosition={tabContextMenuPosition}
        closeMenu={handleTabCloseContextMenu}
        windows={windows}
        groupedTabs={tabList.filter(i => i instanceof GroupedTabs) as GroupedTabs[]}
        onClose={onClose}
        onCloseOtherTabs={onCloseOtherTabs}
        onCloseBelowTab={onCloseBelowTab}
        onNewGroupCreated={handleNewGroupCreated}
      />}
      <TabGroupContextMenu
        group={currentContextMenuGroup}
        tabIds={currentContextualGroupedTabs?.tabs.map(t => t.id!)}
        contextMenuPosition={groupContextMenuPosition}
        onCloseMenu={handleGroupCloseContextMenu}
      />
      {renderPinnedTabs()}
      <ScrollableList ref={scrollableListRef}>
        <DndContext
          sensors={sensors}
          collisionDetection={rectIntersection}
          onDragEnd={onTabMove}
          onDragStart={onDragStart}
          onDragMove={onDragMove}
          onDragOver={onTabDrag}
          onDragCancel={onDragCancel}
          modifiers={[restrictToVerticalAxis]}
          measuring={{ droppable: { strategy: MeasuringStrategy.WhileDragging } }}
        >
          <SortableContext
            items={getItems()}
            strategy={verticalListSortingStrategy}
          >
            {renderGroupedTabs()}
          </SortableContext>
        </DndContext>
        {!isScrolling && <NewTabButton onClick={handleNewTab} />}
      </ScrollableList>
      {isScrolling && <NewTabButton onClick={handleNewTab} />}
    </>
  );
}

export default TabsList;
